<template>
  <div>
    <van-grid :column-num="7" :key="item" v-for="item in keys">
      <van-grid-item  :text="item" />
      <slot name="right">
        <div class="red">
          <van-grid-item  text="文字" />
        </div>
      </slot>
    </van-grid>
  </div>
</template>

<script>

  export default {
      name: 'index',
      components: {

      },
      data() {
          return{
            keys:[]
          }
      },
      created() {
        let old = window.localStorage.getItem("key");
        if(old){
          this.keys = old.split(",");
        }else {
          window.localStorage.setItem("key","1,1,1,1,1,1,1")
          this.keys= window.localStorage.getItem("key").split(",");
        }
      }
  }
</script>

<style>
  .van-grid-item *{
    color: blue;
  }
  .red *{
    color: red;
  }
</style>
